import React from 'react';
import Picker from './scrollComponent';
import {scrollUp, scrollDown} from './onScroll';

const zwsp = 'â€'; //zero width space

export default class ScrollApp extends React.Component {
    constructor(props) {
        super(props);
        this.state = {
            toDo: [zwsp, zwsp, zwsp, zwsp, 'complete website', 'do math book', 'get mmr', '1234', '2234', 'abcd', 'ddde', 'hahah', 'ehehe', zwsp, zwsp, zwsp, zwsp],
            count: 0 //keeps track of +- from center
        };
        this.count = 0; //used to keep track & change of this.state.count
        this.up = this.up.bind(this);
        this.down = this.down.bind(this);
    }

    componentDidMount() {
        this._ismounted = true; //disable features before first render
        this.animating = false; //stop scrolling when animating
        window.addEventListener('wheel', (event) => {//detect scrolling
            if (event.deltaY > 0) { //scrollDown
                this.down();
            } else { //up
                this.up();
            }
        });
    }

    shouldComponentUpdate() {
        for (let i = 0; i < 9; i++) {
            document.getElementById('list' + i).remove();//remove duplicate renders
        }
        this.animating = false;
        return true;
    }

    up() {
        if (this._ismounted && !this.animating && this.state.count > 0) {
            this.animating = true;
            scrollUp();
            this.count--;
            setTimeout(() => {
                this.setState({
                    count: this.count
                });
            }, 500);
        };
    }

    down() {
        if (this._ismounted && !this.animating  && this.state.count < this.state.toDo.length - 9) {
            this.animating = true;
            scrollDown();
            this.count++;
            setTimeout(() => {
                this.setState({
                    count: this.count
                });
            }, 500);
        };
    }

    render() {
        return <Picker list={this.state.toDo} count={this.state.count} up={this.up}/>;
    }
}
