import React from 'react';
import styleList from './scrollStyles';

let list =  []; //list of things to display

export default class Picker extends React.Component {
    render() {
        for (let i = 0; i < 9; i++) {
            list.push(<p onClick={this.props.up} id={'list' + i} style={styleList[i]}>{this.props.list[i + this.props.count]}</p>)
        }; //adds items to wheel to display
        return list;
    }
}
