const viewportHeight = window.innerHeight;
document.getElementById('todo').style.height = viewportHeight + 'px';

let styleList0 = {
    opacity: 0,
    fontSize: '1em',
    top: '21%'
};
let styleList1 = {
    opacity: 0.4,
    fontSize: '1.5em',
    top: '24%'
};
let styleList2 = {
    opacity: 0.6,
    fontSize: '2em',
    top: '28%'
};
let styleList3 = {
    opacity: 0.8,
    fontSize: '2.5em',
    top: '33%'
};
let styleList4 = {
    fontSize: '3em',
    top: '40%'
};
let styleList5 = {
    opacity: 0.8,
    fontSize: '2.5em',
    top: '50%'
};
let styleList6 = {
    opacity: 0.6,
    fontSize: '2em',
    top: '58%'
};
let styleList7 = {
    opacity: 0.4,
    fontSize: '1.5em',
    top: '65%'
};
let styleList8 = {
    opacity: 0,
    fontSize: '1em',
    top: '71%'
};

let styleList = [styleList0, styleList1, styleList2, styleList3, styleList4, styleList5, styleList6, styleList7, styleList8];

for (let i = 0; i < 9; i++) {
    styleList[i]['position'] = 'absolute';
    styleList[i]['left'] = '0';
    styleList[i]['right'] = '0';
    styleList[i]['transition'] = 'all 0.2s ease-in-out';
    styleList[i]['-webkit-transition'] = 'all 0.2s ease-in-out';
    styleList[i]['-moz-transition'] = 'all 0.2s ease-in-out';
}

export default styleList;
